import React, { useState } from "react";

const ThumbnailContainer = (props) => {
  const [startNum, setStartNum] = useState(0);
  const [contentsArr, setContentsArr] = useState();

  const extractContents = () => {
    let tmp = [];
    for (let i = 0; i < 3 && i + startNum < props.contents.length; i++) {
      tmp.push(props.contents[startNum + i]);
    }
    return tmp;
  };
  if (contentsArr === undefined) {
    setContentsArr(extractContents());
  }
  return <div className="thumbnail-container">{contentsArr}</div>;
};

export default ThumbnailContainer;
