import React from "react";
import HomeContents from "./HomeContents";
import Thumbnail from "./Thumbnail";
import ThumbnailContainer from "./ThumbnailContainer";

const Container = () => {
  let onGoing = [];
  for (let i = 0; i < 5; i++) {
    onGoing.push(<Thumbnail key={i} />);
  }

  return (
    <div className="container">
      <div className="container-h">
        <HomeContents title="진행중인 프로젝트">
          <ThumbnailContainer contents={onGoing}></ThumbnailContainer>
        </HomeContents>
        <HomeContents title="완료된 프로젝트">
          <ThumbnailContainer contents={onGoing}></ThumbnailContainer>
        </HomeContents>
        <HomeContents title="개발일지">
          <ThumbnailContainer contents={onGoing}></ThumbnailContainer>
        </HomeContents>
      </div>
    </div>
  );
};

export default Container;
