import React, { useState, useEffect, forwardRef, useRef } from "react";
import GitHubIcon from "@material-ui/icons/GitHub";
import MenuIcon from "@material-ui/icons/Menu";

const Navigation = (props) => {
  return (
    <div className={props.classname}>
      <div className="nvg-icon-svg"></div>
      <div className="nvg-name">WAQESD</div>
      <div className="nvg-info">
        한양대학교<br></br>컴퓨터소프트웨어학부
      </div>
      <div className="nvg-divider"></div>
      <button className="nvg-menu"> 진행중인 프로젝트</button>
      <button className="nvg-menu"> 완료된 프로젝트</button>
      <button className="nvg-menu"> 개발일지</button>
    </div>
  );
};

const Header = forwardRef(() => {
  const nvgRef = useRef();
  const [nvg, setNvg] = useState();
  let navigation = undefined;
  const handleClickOutside = ({ target }) => {
    if (nvg === "nvg-open" && !nvgRef.current.contains(target))
      setNvg("nvg-close");
  };

  useEffect(() => {
    window.addEventListener("click", handleClickOutside);
    return () => {
      window.removeEventListener("click", handleClickOutside);
    };
  });

  return (
    <div className="header">
      <div className="nvg-wrapper" ref={nvgRef}>
        {nvg ? <Navigation classname={nvg} /> : undefined}
      </div>
      <button
        className="menu-button"
        onClick={() => {
          setNvg("nvg-open");
        }}
      >
        <MenuIcon />
      </button>
      <a className="header-title" href="https://waqesd.github.io/">
        WAQESD의 기술 블로그
      </a>
      <div className="header-icons">
        <button className="github-button">
          <a
            href="https://github.com/WAQESD"
            target="_blink"
            style={{ color: "black" }}
          >
            <GitHubIcon />
          </a>
        </button>
      </div>
    </div>
  );
});

export default Header;
